<template>
  <div class="blog-container">
    <div class="blog-pages">
      <div class="col-md-12 panel">
        <div class="panel-body">
          <h2 class="text-center"></h2>
          <hr>
          <div data-validator-form>
            <div class="form-group">
              <input v-model.trim="title" v-validator.required="{ title: '' }" type="text" class="form-control" placeholder="" @input="saveTitle">
            </div>
            <div class="form-group">
              <textarea id="editor"></textarea>
            </div>
            <br>
            <div class="form-group">
              <button class="btn btn-primary" type="submit" @click="post"> </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import SimpleMDE from 'simplemde'
  import hljs from 'highlight.js'
  import ls from "../../utils/localStorage";

  window.hljs = hljs

  export default {
    name: 'Create',
    data() {
      return {
        title: '',
        content: ''
      }
    },
    mounted() {
      const simplemde = new SimpleMDE({
        element: document.querySelector('#editor'),
        placeholder: ' Markdown  ;-)',
        spellChecker: false,
        autoDownloadFontAwesome: false,
        autosave: {
          enable: true,
          uniqueId: 'vuejs-essential',
        },
        renderingConfig: {
          codeSyntaxHighlighting: true
        }
      })

      simplemde.codemirror.on('change', () => {
        this.content = simplemde.value()
      })

      this.simplemde = simplemde
      this.fillContent()
    },
    methods: {
      saveTitle() {
        ls.setItem('smde_title', this.title)
      },
      fillContent() {
        const simplemde = this.simplemde
        const title = ls.getItem('smde_title')

        if (title != null) {
          this.title = title
        }

        this.content = simplemde.value()
      },
      post() {
        const title = this.title
        const content = this.content

        if (title !== '' && content.trim() !== '') {
          const article = {
            title,
            content
          }

          this.$store.dispatch('post', { article })

          this.clearData()
        }
      },
      clearData() {
        this.title = ''
        ls.removeItem('smde_title')
        this.simplemde.value('')
        this.simplemde.clearAutosavedValue()
      }
    }
  }
</script>

<style scoped>

</style>



